<template>
  <v-card width="600" class="mx-auto mt-15">
    <v-row>
      <v-spacer></v-spacer>
      <v-img
        class="mt-10 mb-5"
        lazy-src="https://bcas.lk/wp-content/themes/Divi/images/logo.png"
        max-height="150"
        max-width="250"
        src="https://bcas.lk/wp-content/themes/Divi/images/logo.png"
      ></v-img>
      <v-spacer></v-spacer>
    </v-row>
    <v-divider></v-divider>
    <v-alert
      class="mt-2 mr-2 ml-2"
      dense
      outlined
      type="error"
      v-if="loginSuccess === false"
    >
      <strong>Login Failed.</strong> Please enter a valid email and password!
    </v-alert>
    <v-card-text>
      <v-form>
        <v-text-field
          label="Email"
          prepend-icon="mdi-account-circle"
          v-model="email"
        />
        <v-text-field
          :type="showPassword ? 'text' : 'password'"
          label="Password"
          v-model="password"
          prepend-icon="mdi-lock"
          :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
          @click:append="showPassword = !showPassword"
        />
      </v-form>
    </v-card-text>
    <v-divider></v-divider>
    <v-card-actions>
      <v-spacer></v-spacer>
      <span class="mr-5">
        <router-link to="/reset-password">
          Forgot password ?
        </router-link>
      </span>
      <v-btn color="info" @click="loginHandler">Login</v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "Home",
  data: () => ({
    showPassword: false,
    loginSuccess: null
  }),
  computed: {
    ...mapGetters("auth", ["getLoggedIn"]),
    email: {
      get() {
        return this.$store.state.auth.user.email;
      },
      set(value) {
        this.$store.commit("auth/updateEmail", value);
      }
    },
    password: {
      get() {
        return this.$store.state.auth.user.password;
      },
      set(value) {
        this.$store.commit("auth/updatePassword", value);
      }
    },
    deviceName: {
      get() {
        return this.$store.state.auth.user.deviceName;
      },
      set(value) {
        this.$store.commit("auth/updateDeviceName", value);
      }
    }
  },
  methods: {
    ...mapActions("auth", ["login"]),
    async loginHandler() {
      this.deviceName = "WebApp";
      await this.login()
        .then(() => {
          this.$router.push("/");
        })
        .catch(() => {
          this.loginSuccess = false;
          console.log("Login failed...");
        });
    }
  }
};
</script>
